

























































import Vue from 'vue';
import {Component, Prop} from "vue-property-decorator";
import ToggableSideMenuItem from "@/components/sidemenu/ToggableSideMenuItem.vue";

interface MobileMenuConfigInterface extends Record<string, any> {
  eatMenu: Record<string, any>[]
}

@Component({
  components: {ToggableSideMenuItem}
})
export default class UgolekMobileSideMenu extends Vue {
  @Prop({required: true, default: {eatMenu: []}})
  config!: MobileMenuConfigInterface;

  showBody = false;
  showEatmenu = false;

  clickTab(tab: any, idx: number) {
    this.$emit('click-tab', tab, idx);
    this.showBody = false;
  }

  toggleMenu() {
    this.showBody = !this.showBody;
  }

  toggleEatmenu() {
    this.showEatmenu = !this.showEatmenu;
  }

  get eatmenuClass() {
    return {
      'eatmenu-opened': this.showEatmenu,
      'eatmenu-closed': !this.showEatmenu,
    }
  }

  showMenu() {
    this.showBody = true;
  }

  get burgerClass() {
    return {
      'burger-opened': this.showBody
    }
  }

  get sideMenuClass() {
    return {
      'ums-opened': this.showBody
    }
  }

  closeMenu() {
    this.showBody = false;
  }

  get chevronClass() {
    return {
      'gg-chevron-up': this.showEatmenu,
      'gg-chevron-down': !this.showEatmenu
    };
  }
}
